.profile {
	text-align: center;
	border: 1px solid;
	width: 250px;
	margin: auto;
	border-radius: 5px;
}
.description {
	border-bottom: 1px solid;
}
.avatar-profile {
	width: 75px;
	border-radius: 50px;
	margin-top: 40px;
}
.name {
	line-height: 1.3;
}
.tag {
	color: gray;
}
.location {
	color: gray;
}
.stats {
	display: flex;
	margin: 0;
	padding: 0;
}
.stats-item {
	display: flex;
	flex-wrap: wrap;
	background-color: lightgray;
	padding: 10px 0;
}
.stats-item:not(:last-child) {
	border-right: 1px solid;
}

.label {
	font-size: 12px;
	margin: auto;
	color: gray;
}
.quantity {
	margin: auto;
}
